body{
  background-color: #2c3e50;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}
#drum-machine{
  background-color: #95a5a6;
  height: 500px;
  width: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: solid black 5px;
}
#display{
  background-color: #bdc3c7;
  width: 180px;
  height: 40px;
  text-align: center;
  padding-top: 5px;
  border: solid black 3px;
}
#drumsContainer{
  width: 250px;
  height: 300px;
  padding: 5px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
